<template>
  <div class="resultStyle">
      <p class="likeTitle"><span class="likeSpan"></span> </p>
      <div class="content">
          <div class="outlineLeft" :style="{background:textColor}"></div>
          <div class="outlineBottom" :style="{background:textColor}"></div>
          <div class="subject titEleven" v-if="dataJson.sleep == 0">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P></P>
            <P><span :style="{color:textColor}" class="subject-color"></span></P>
          </div>
          <div class="subject titEleven" v-if="dataJson.sleep == 1">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P></P>
            <P></P>
          </div>
          <div class="subject titEleven" v-if="dataJson.sleep == 2">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P></P>
            <P><span :style="{color:textColor}" class="subject-color"></span></P>
          </div>
          <div class="subject titEleven" v-if="dataJson.sleep == 3">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P><span :style="{color:textColor}" class="subject-color"></span></P>
            <P><span :style="{color:textColor}" class="subject-color"></span></P>
          </div>
          <div class="subject titEleven" v-if="dataJson.sleep == 4">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P></P>
            <P></P>
          </div>
          <div class="subject titEleven" v-if="dataJson.sleep == 5">
            <p><span :style="{color:textColor}" class="subject-color"></span></p>
            <P></P>
            <P></P>
          </div>

          <div class="subject">
            <p v-if="dataJson.tooThpaste == 0" ><span :style="{color:textColor}" class="subject-color"></span></p>
            <p v-if="dataJson.tooThpaste == 1" ><span :style="{color:textColor}" class="subject-color"></span></p>
            <p v-if="dataJson.tooThpaste == 2" ><span :style="{color:textColor}" class="subject-color"></span></p>
            <p v-if="dataJson.tooThpaste == 3" ><span :style="{color:textColor}" class="subject-color"></span></p>
          </div>
          <div class="subject">
              <p v-if="dataJson.dosome <=13 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.dosome <=13 "></p>
              <p v-if="dataJson.dosome >13 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.dosome >13 "></p>
          </div>
          <div class="subject">
              <p v-if="dataJson.try ==0 "></p>
              <p v-if="dataJson.try ==0 "></p>
              <p v-if="dataJson.try <=9 && dataJson.try>=3 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.try <=9 && dataJson.try>=3 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.try >9 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.try >9 "></p>

              <p v-if="dataJson.mustHave <12 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.mustHave <12 "></p>
              <p v-if="dataJson.mustHave >=12 "><span :style="{color:textColor}" class="subject-color"></span></p>
              <p v-if="dataJson.mustHave >=12 "></p>
          </div>
          <div class="subject last">
              <p>-IN</p>
          </div>
      </div>
  </div>
</template>
<script>
export default {
    props: ['dataJson'],
    data(){
        return{
            textColor:''
        }
    },
    beforeUpdate(){
        this.getColor()

    },
    created() {
        
    },
    methods: {
      getColor(){
          var textColorNum = this.dataJson.choiceColor;
          var textColorArr= ['#e79e92','#deb844','#679cb6','#9eb58d','#fb7472','#7ccbe2'];   
          this.textColor = textColorArr[textColorNum];
      }
    }
}
</script>
<style scoped>
.resultStyle{
    position: relative;
    margin-top: -.4rem;
    margin-left :.2rem;
    margin-right:.2rem;
}
.resultStyle .likeTitle{
    margin-top:0;
    margin-bottom: .4rem;
    text-align: left;
    color:#666;
    line-height: .2rem;
    font-size: .2rem;
    /* text-decoration: underline; */
}
.likeSpan{
    padding-bottom:4px;
    border-bottom: 2px solid #666;
}
.content{
    position: relative;
    margin-left:.07rem;
    -webkit-box-shadow: #ccc 4px 3px 5px;;
    -moz-box-shadow: #ccc 4px 3px 5px;;
    box-shadow: #ccc 4px 3px 5px;
    background: #fff;
    padding-bottom:.35rem;
    border:#eee 1px solid;
}
.outlineLeft{
    position: absolute;
    left:-.1rem;
    bottom:-.1rem;
    width:.04rem;
    height:3.6rem;
    /* background: #9eb58d; */
    z-index: 999;
}
.outlineBottom{
    position: absolute;
    left:-.1rem;
    bottom:-.1rem;
    width:2rem;
    height:.04rem;
    /* background: #9eb58d; */
    z-index: 989;
}
.titleLeft{
    margin-top:0;
    margin-bottom: .2rem;
    text-align: left;
    color:#222;
    line-height: .2rem;
    font-size: .2rem;
}
.subject{
    margin-top:.35rem;
    padding-top:.25rem;
}
.subject .subject-color{
    /* color:#9eb58d; */
}
.subject p{
    margin-top:0;
    margin-bottom: 0;
    margin-right: .34rem;
    line-height: .3rem;
    font-size: .16rem;
    text-align: right;
    color:#666;
}
.last {
    padding-right:.15rem;
}
</style>




