<template>
    <div class="resultStyle">
        <!-- <div class="shareIcon"></div> -->
        <p class="likeTitle"><span class="likeSpan"></span></p>
        <ul class="list">
            <p class="titleLeft">{{likeStyle[0]}}</p>
            <li>
                <div class="cont">
                    <div class="imgWrap">
                        <div id="imgAnimate1" v-bind:class="[imgAnimate[1].isShow ? 'isShow' : '', 'imgAnimate']" v-if="strArr[0] == index" v-for="(item, index) in message.dataList" v-bind:style="'background-image:url(http://owxa0vmjl.bkt.clouddn.com/style'+message.dataList[index].src"></div>

                    </div>
                </div>
            </li>
            <li>
                <div class="cont">
                    <div class="imgWrap">
                        <div id="imgAnimate2" v-bind:class="[imgAnimate[2].isShow ? 'isShow' : '', 'imgAnimate']" v-if="strArr[0] == index" v-for="(item, index) in message.dataList" v-bind:style="'background-image:url(http://owxa0vmjl.bkt.clouddn.com/style'+message.dataList[index].src1"></div>
                    </div>
                </div>
            </li>
        </ul>
        <ul class="list">
            <p class="titleLeft">{{likeStyle[1]}}</p>
            <li>
                <div class="cont">
                    <div class="imgWrap">
                        <div id="imgAnimate3" v-bind:class="[imgAnimate[3].isShow ? 'isShow' : '', 'imgAnimate']" v-if="strArr[1] == index" v-for="(item, index) in message.dataList" v-bind:style="'background-image:url(http://owxa0vmjl.bkt.clouddn.com/style'+message.dataList[index].src"></div>
                    </div>
                </div>
            </li>
            <li>
                <div class="cont">
                    <div class="imgWrap">
                        <div id="imgAnimate4" v-bind:class="[imgAnimate[4].isShow ? 'isShow' : '', 'imgAnimate']" v-if="strArr[1] == index" v-for="(item, index) in message.dataList" v-bind:style="'background-image:url(http://owxa0vmjl.bkt.clouddn.com/style'+message.dataList[index].src1"></div>
                    </div>
                </div>
            </li>
        </ul>
        <div class="mt40">
            <div class="btn">
                     <div class="cont" @click="shareLayer" v-if="isFromShare">&nbsp;&nbsp;&nbsp;</div>
               <div v-else>
                    <router-link to="/shareStart" v-if="isShare">
                    <a class="cont">&nbsp;&nbsp;&nbsp;</a>
                </router-link>
                <div class="cont" @click="postResult" v-else>&nbsp;&nbsp;&nbsp;&nbsp;IN</div>
               </div>
            </div>
        </div>
        <div class="layerShare" v-if="isLayer" @click="shareLayer" id="layerShare">
          <div class="shareImg">
            <img src="http://ovfllimsi.bkt.clouddn.com/Guideshare.png" >
          </div>
        </div>
    </div>
</template>
<script>
import wx from 'weixin-js-sdk'
export default {
  name: "likeStyle",
  props: ["dataJson", "imgAnimate"],

  data() {
    return {
      isShare: false, //APP
      isFromShare: false, //APP
      isLayer: false,
      strArr: [],
      likeStyle: [],
      message: {
        dataList: [
          {
            name: "",
            src: "l-1-1.png",
            src1: "l-1-2.png"
          },
          {
            name: "",
            src: "l-2-1.png",
            src1: "l-2-2.png"
          },
          {
            name: "",
            src: "l-3-1.png",
            src1: "l-3-2.png"
          },
          {
            name: "",
            src: "l-4-1.png",
            src1: "l-4-2.png"
          },
          {
            name: "",
            src: "l-5-1.png",
            src1: "l-5-2.png"
          },
          {
            name: "",
            src: "l-6-1.png",
            src1: "l-6-2.png"
          },
          {
            name: "",
            src: "l-7-1.png",
            src1: "l-7-2.png"
          },
          {
            name: "",
            src: "l-8-1.png",
            src1: "l-8-2.png"
          },
          {
            name: "",
            src: "l-9-1.png",
            src1: "l-9-2.png"
          },
          {
            name: "",
            src: "l-10-1.png",
            src1: "l-10-2.png"
          },
          {
            name: "",
            src: "l-11-1.png",
            src1: "l-11-2.png"
          },
          {
            name: "",
            src: "l-12-1.png",
            src1: "l-12-2.png"
          }
        ]
      }
    };
  },
  mounted() {
    this.shareDuge();
    setTimeout(() => {
      this.allStyle();
      this.isFromShare = this.dataJson.fromShare - 0;
      this.shareWx();
   
    }, 500);
  },

  created() {
    if(this.$bridge){
       this.$bridge.callHandler(
      "enterLastPage",
      { testResult: { result: 1 } },
      function(data) {}
    );
    }
   
  },
  methods: {
    allStyle() {
      this.strArr = decodeURIComponent(this.dataJson.likeStyle).split(",");
      this.strArr[0] = decodeURIComponent(this.strArr[0]);
      this.likeStyle[0] = decodeURIComponent(this.strArr[0]);
      this.strArr[1] = decodeURIComponent(this.strArr[1]);
      this.likeStyle[1] = decodeURIComponent(this.strArr[1]);
      var styleAll = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ];
      styleAll.forEach((item, index) => {
        if (this.strArr[0] == item) {
          this.strArr[0] = index;
        }
        if (this.strArr[1] == item) {
          this.strArr[1] = index;
        }
      });
    },
    shareDuge() {
      if (window.location.href.lastIndexOf("shareResult") != -1) {
        this.isShare = true;
      }
    },
    //
    showGuide() {},
    postResult() {
      var _likeStyle = decodeURIComponent(this.dataJson.likeStyle).split(",");
      console.log({
        likeStyle: _likeStyle,
        houseArea: decodeURIComponent(this.dataJson.houseArea),
        result: 1
      });
      this.$bridge.callHandler(
        "callWithDict",
        {
          testResult: {
            likeStyle: _likeStyle,
            houseArea: decodeURIComponent(this.dataJson.houseArea),
            result: 1
          }
        },
        function(data) {}
      );
    },
    parseQueryString(url) {
      var obj = {};
      var keyvalue = [];
      var key = "",
        value = "";
      var paraString = url
        .substring(url.indexOf("?") + 1, url.length)
        .split("&");
      for (var i in paraString) {
        keyvalue = paraString[i].split("=");
        key = keyvalue[0];
        value = keyvalue[1];
        obj[key] = value;
      }
      return obj;
    },

    shareLayer() {
      var height = window.screen.height;

      this.isLayer = !this.isLayer;
    },
    shareWx() {
      this.getWxAppid();
      this.shareReady();
    },
    getWxAppid() {
      var url = "http://bos.foreverlynn.com/weixin/getWXUrl";
      this.$jsonp(url,{url: location.href.replace(location.hash, "")})
        .then(res => {
          if (res.code != "200") {
            return
          }
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: res.data.result.appId, // 
            timestamp: res.data.result.timestamp, // 
            nonceStr: res.data.result.nonceStr, // 
            signature: res.data.result.signature, // 
            jsApiList: [
              "onMenuShareTimeline",
              "onMenuShareAppMessage",
              "startRecord",
              "stopRecord",
              "onVoiceRecordEnd",
              "playVoice",
              "pauseVoice",
              "stopVoice",
              "onVoicePlayEnd",
              "uploadVoice",
              "downloadVoice",
              "chooseImage",
              "previewImage",
              "uploadImage",
              "downloadImage"
            ] // JS
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    shareReady() {
      wx.ready(function() {
        //  API
        wx.onMenuShareAppMessage({
            link:'http://wesetup.cn/chatactsTest/index.html#/shareStart',
          title: "", // 
          desc: "", // 
          imgUrl: "http://ovfllimsi.bkt.clouddn.com/logo.png", // 
          success: function(success) {
              console.log(success);
            // 
          },
          cancel: function() {
            // 
          }
        });

        wx.onMenuShareTimeline({
         link:'http://wesetup.cn/chatactsTest/index.html#/shareStart',
          title: "", // 
          desc: "", // 
          imgUrl: "http://ovfllimsi.bkt.clouddn.com/logo.png", // 
          success: function(success) {
             console.log(success);
            // 
          },
          cancel: function() {
            // 
          }
        });
        wx.error(function(res) {
          console.log(res);
        });
      });
    }
  }
};
</script>
<style scoped>
.resultStyle {
  margin-top: 1rem;
  margin-left: 0.1rem;
  margin-right: 0.1rem;
}
.layerShare {
  position: fixed;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.7;
  z-index: 9999;
  top: 0;
}
.shareImg {
  position: absolute;
  top: 0%;
  width: 2rem;
  right: 0.2rem;
  z-index: 9999999;
}
.shareImg img {
  width: 100%;
}
.shareIcon {
  position: absolute;
  width: 40px;
  height: 40px;
  background: url(http://ovfllimsi.bkt.clouddn.com/Guide%20share@2x.png);
}
.resultStyle .likeTitle {
  margin-top: 0;
  margin-bottom: 0.4rem;
  text-align: left;
  color: #666;
  line-height: 0.2rem;
  font-size: 0.2rem;
  /* text-decoration: underline; */
}
.likeSpan {
  padding-bottom: 4px;
  border-bottom: 2px solid #666;
}
.titleLeft {
  margin-top: 0.4rem;
  margin-bottom: 0.2rem;
  text-align: left;
  color: #222;
  line-height: 0.2rem;
  font-size: 0.2rem;
}

img {
  width: 100%;
  display: block;
}

.list,
.list li {
  padding: 0;
  list-style-type: none;
}

.list li {
  margin: 0.1rem 0;
}

.cont {
  width: 100%;
}

.mt40 {
  margin-top: 40px;
}

.btn {
  position: relative;
  bottom: 0.08rem;
  width: 100%;
}

.btn .cont {
  /* margin: 0 .2rem .15rem .2rem; */
  color: #fff;
  font-size: 0.17rem;
  border-radius: 0.04rem;
  line-height: 0.44rem;
  height: 0.44rem;
  background: #43bb57;
}

.btn a.cont {
  display: inline-block;
  text-decoration: none;
}
</style>






